module.exports = ".chats-content-div {\n  background: #fff;\n  border-radius: 50px 50px 0px 0px;\n  top: -60px;\n  position: relative;\n  width: 100%;\n}\n\n.head-title {\n  color: #fff;\n  font-size: 16px;\n  font-weight: bold;\n}\n\n.chats-empty-div {\n  background: #0A15D5;\n  height: 120px;\n}\n\n.chats .custom-icon-chat-icon-1 {\n  font-size: 40px;\n  color: white;\n}\n\n.chats-list {\n  background: none;\n  border-radius: 50px;\n  padding: 0;\n  margin: 12px 10px 12px 45px;\n  box-shadow: -7px 10px 18px 7px rgba(0, 0, 0, 0.14);\n}\n\n.chats-list .chats-content {\n  padding: 10px 0px 10px 4px;\n}\n\n.chats-content {\n  top: 0;\n  width: 100%;\n}\n\n.chats-timeline {\n  position: relative;\n  max-width: 1200px;\n  margin: 0 auto;\n  top: 30px;\n  padding-right: 10px;\n}\n\n/* The actual timeline (the vertical ruler) */\n\n.chats-timeline::after {\n  content: \"\";\n  position: absolute;\n  width: 2px;\n  background-color: #D2D2D2;\n  top: 0;\n  bottom: 0;\n  left: 50%;\n}\n\n/* Container around content */\n\n.chats-container {\n  position: relative;\n  background-color: inherit;\n}\n\n/* The circles on the timeline */\n\n.chats-container::after {\n  content: \"\";\n  position: absolute;\n  width: 15px;\n  height: 15px;\n  top: 27px;\n  border-radius: 50%;\n  z-index: 1;\n  margin-left: 9px;\n}\n\n.chats-container:nth-of-type(odd)::after {\n  background-color: #0A15D5;\n}\n\n.chats-container:nth-of-type(even)::after {\n  background-color: #FF9F55;\n}\n\n/* Place the container to the left */\n\n.left {\n  left: 0;\n}\n\n/* Place the timelime to the left */\n\n.chats-timeline::after {\n  left: 31px;\n}\n\n/* Make sure all circles are at the same spot */\n\n.left::after,\n.right::after {\n  left: 15px;\n}\n\n.chats-content .name {\n  font-family: \"Poppins-Regular\";\n  font-size: 14px;\n  white-space: nowrap;\n  overflow: hidden;\n  text-overflow: ellipsis;\n}\n\n.chats-content .unreed {\n  font-size: 10px;\n  margin: auto;\n  border-radius: 50px;\n  padding: 0px 1